<template>
    <div :id="type"></div>
</template>

<script setup>
import * as echarts from 'echarts'
import { ref, reactive, onMounted,defineExpose } from 'vue'
import { fontSize } from '@/util/util.js'

let props = defineProps({
      type:{
        type:String
      },
      simData:{
        type:Object
      },
      xAxisData:{
        type:Array
      }
    })
let colors = ['#6ad8ff','#FFC56C', 'rgba(0,255,127, 1)','#a6d8ff']

let chart

let options = {
    title:{
      left:0,
      textStyle:{
        color:colors[0],
        fontSize:fontSize(10),
        fontWeight:'normal'
      }
    },
    xAxis: {
      type: 'category',
      axisTick:{
        show:false
      },
      axisLine:{
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      splitLine:{
        show:false
      },
      axisLabel:{
        fontSize:fontSize(10),
        interval:18
      },
    },
    yAxis: {
      type: 'value',
      splitLine:{
        show:false
      },
      axisLine:{
        show:true,
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
      splitNumber:3,
      max:function (value) {
            return parseInt(value.max + 20);
          },
      min:function (value) {
            return parseInt(value.min - 20);
          }
    },
    legend:{
        icon: 'circle',
        top:0,
        itemWidth:fontSize(10),
        textStyle: {
            color: colors[0],
            fontSize:fontSize(10)
        },
    },
    tooltip:{
        show:true,
        trigger:'axis',
        snap:true,
        axisPointer:{
          type:'line',
        }
    },
    
    series: [],
    // grid:{
    //   show:false,
    //   left:'0%',
    //   right:'5%',
    //   top:'10%',
    //   bottom:'10%',
    //   containLabel: true
    // }
}
onMounted(() => {
    let title = ''
    let legend = ['A相','B相','C相']
    switch(props.type){
        case "current":
            title = '电流'
            break;
        case "power":
            title = '功率'
            legend = ['有功功率','无功功率']
            break;
    }
    options.title.text = title
    legend.map((item,index) => {
       options.series.push({
          name: item,
          type: 'line',
          smooth: true,
          symbol:"none",
          itemStyle:{
            color:colors[index]
          },
          lineStyle:{
            width:fontSize(2)
          },
          // areaStyle: {
          //   color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          //     {
          //       offset: 0,
          //       color: 'rgb(255, 158, 68)'
          //     },
          //     {
          //       offset: 1,
          //       color: 'rgb(255, 70, 131)'
          //     }
          //   ])
          // },
        })
    })
    chart = echarts.init(document.getElementById(props.type));
    chart.setOption(options);
    window.addEventListener('resize',() => {
      chart.resize()
    })
})
function setCharts(){
    let keys = Object.keys(props.simData)
    keys.map((key,index) => {
      options.series[index].data = props.simData[key]
    })
    
    options.xAxis.data = props.xAxisData
    chart.setOption(options);
}

defineExpose({
  setCharts
})




</script>

