<template>
  <div id="login">
    <div id="title">数字实时仿真展示平台</div>
    <div id="loginCenter">
      <span id="loginTitle">用户登录</span>
      <el-form 
        id="loginForm"
        :model="formData"
        :rules="rules"
        ref="ruleFormRef">
        <el-form-item
          prop="username">
          <el-input 
            v-model="formData.username"
            placeholder="用户名"/>
        </el-form-item>
        <el-form-item
          prop="password">
          <el-input 
            v-model="formData.password" 
            type="password"
            placeholder="密码"
            :show-password="true"/>
        </el-form-item>
        <el-form-item>
          <div id="loginRegister">
            <el-button
              type="primary"
              plain
              @click="login(ruleFormRef,registerForm)">
              注册
            </el-button>
            <el-button
              type="primary"
              plain
              @click="login(ruleFormRef,loginForm)">
              登录
            </el-button>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script setup>
import { onMounted,reactive,ref, onBeforeUnmount } from 'vue';
import { throttle } from '../util/util.js';
import { useRouter } from 'vue-router'
import { url } from '../websocketConfig.js';
import { ElMessage } from 'element-plus';
const formData = reactive({
  username:'',
  password:''
}) 
let subscribes = []
let registerForm
let loginForm
const userRouter = useRouter();
const ruleFormRef = ref(null);
const rules = reactive({
  username:[
    {
      required: true,
      message: '请输入用户名',
      trigger: 'change',
    }
  ],
  password:[
    {
      required: true,
      message: '请输入密码',
      trigger: 'change',
    }
  ]
})

onMounted(() =>{
  registerForm = throttle(sendData,3)
  loginForm = throttle(sendData,3)
})
onBeforeUnmount(() => {
  subscribes.map(item => {item.unsubscribe()})
})

//表单校验
function login(formEl,fn){
  if (!formEl) return
  formEl.validate((valid) => {
    if(valid){
      fn()
    }
  })
}

//提交表单
function sendData(){
  // client.publish({
  //   destination:'/app/createProgram',
  //   body:JSON.stringify(formData),
  // })
  userRouter.push({ path: '/map' ,replace:true})
  // localStorage.setItem("isLogin",true)
}



function handleData(msg){
 userRouter.replace('/map')
  ElMessage({
              message: msg.body,
              type: 'success',
            })
}

</script>

<style lang="less">
#login{
  height: 100vh;
  width: 100%;
  position: relative;
  background-image: url(../assets/loginBackground.jpg);
  background-size: 100% auto ;
  font-family:  'SimSun';
  #title{
    position: absolute;
    font-size: 5vh;
    left: 50%;
    top: 15%;
    transform: translateX(-50%);
  }
}
#loginCenter{
  height: 30%;
  width: 20%;
  min-height: 200px;
  background-color: #FFFFFF;
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translateX(-50%);
  padding: 1vh 2vh;
  border-radius: 1vh;
  #loginTitle{
    display: block;
    width: 100%;
    text-align: center;
    font-size: 3vh;
    margin-top: 1vh;
  }
}
#loginForm{
  height: 85%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  .el-input{
    height: 3vh;
    font-size: 1vh;
  }
}
#loginRegister{
  width: 100%;
  display: flex;
  justify-content: space-between;
  .el-button{
    width: 45%;
    height: 3vh;
    font-size: 1vh;
  }
}
</style>