import { Client } from "@stomp/stompjs";
import SockJS from "sockjs-client";

let url = "http://192.168.3.43:8080"
// let url = "http://192.168.3.2:8080"
// let url = "http://localhost:8080"
function ConnectWebsocket(){
    this.client = new Client({
        reconnectDelay:1000,
        heartbeatIncoming:5000,
        heartbeatOutgoing:60000,
        connectHeaders:{
           username:"admin",
           password:"admin"
        },
        onConnect:() => {
            console.log('连接成功');
        },
        webSocketFactory:() => {
            return new SockJS(url+'/zj-websocket')
        },
        onStompError:(frame) => {
            // console.log('onStompError');
            // console.log(frame);
        },
        onWebSocketError:(frame) => {
            // console.log('onWebSocketError');
            // console.log(frame);
        },
        onDisconnect:(frame) => {
            // console.log('onDisconnect');
            // console.log(frame);
        },
        onWebSocketClose:(frame) => {
            // console.log('onWebSocketClose');
            // console.log(frame);
        },
        onChangeState:(state) => {
            // console.log('onChangeState'+state);
        }
    })
}

export { ConnectWebsocket,url}