//防抖
function debounce(fn,delay){
    var timeout = null;
    return function(){
              let that = this;
              let args = arguments;
              clearTimeout(timeout);
              timeout = setTimeout(() => {
                fn.apply(that,args)
              },delay*1000)
            }
}
//节流
function throttle(fn,delay){
    let timeout = null;
    return function(){
                if(!timeout){
                    let that = this;
                    let args = arguments;
                    fn.apply(that,args);
                    timeout = setTimeout(() => {
                        // fn.apply(that,args);
                        timeout = null;
                    },delay*1000)
                }
            }
}
//echarts字体计算
function fontSize(res){
	const clientWidth = window.innerHeight||document.documentElement.clientHeight||document.body.clientHeight;
	if (!clientWidth) return;
	let fontSize = clientWidth / 1080;
	return res*fontSize;
}
//间隔时间点
function timeSlot(step){
    var date = new Date()
    date.setHours(0)    // 时分秒设置从零点开始
    date.setSeconds(0)
    date.setUTCMinutes(0)
    
    var arr = [], timeArr = [];
    var slotNum = 24*60/step   // 算出多少个间隔
    for (var f = 0; f < slotNum; f++) {   //  stepM * f = 24H*60M
        // arr.push(new Date(Number(date.getTime()) + Number(step*60*1000*f)))   //  标准时间数组
        var time = new Date(Number(date.getTime()) + Number(step*60*1000*f))  // 获取：零点的时间 + 每次递增的时间
        var hour = '', sec = '';
        time.getHours() < 10 ? hour = '0' + time.getHours() : hour = time.getHours()  // 获取小时
        time.getMinutes() < 10 ? sec = '0' + time.getMinutes() : sec = time.getMinutes()   // 获取分钟
        timeArr.push(hour + ':' + sec)
    }
    return timeArr
}
function currentMonthDays(date1) {
    // 获取标准时间
    const date = new Date(date1);
    // 获取当天日期
    const currentDay = date.getDate();
    // 获取当前月份（实际月份需要加1）
    const currentMonth = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
    // 获取当前年份
    const currentYear = date.getFullYear();
    // 获取当前月有多少天
    const currentMonthDays = new Date(currentYear, currentMonth, 0).getDate();
    // 当前月份所有日期集合
    const currentMonthArr = [];
    for (let day = 1; day <= currentMonthDays; day++) {
        // 截至当前日期为止
        // if (day <= currentDay) {
            let dateItem = currentYear + "-" + currentMonth + "-" + (day < 10 ? '0' + day : day)
            currentMonthArr.push(dateItem)
        // }
    }
    return currentMonthArr;
}

