<template>
    <div id="rightData">
        <div>
            <div class="searchHead">
                <div style="width: fit-content;">
                    <span class="name" @click="pointDialog = true">{{pointName}}</span>
                </div>
                <PointDialog
                    @selectedPointNum="selectedPointNum"></PointDialog>
            </div>
            <div class="border pointData">
                <div class="charts">
                    <VoltageChart 
                        :simData="voltage" 
                        :xAxisData="xAxisPoint"
                        :voltageLevel="voltageLevel"
                        ref="voltageRef"
                        style="height: 100%;"></VoltageChart>
                    <!-- <Charts 
                        :type="'voltageFre'" 
                        :title="'频域'" 
                        :simData="voltageFre"
                        ref="voltageFreRef" 
                        style="height: 50%;"></Charts> -->
                </div>
                <div style="display: flex; flex-direction: column; justify-content: space-evenly;">
                    <span style="text-align: center;">电压(kV)</span>
                    <div class="qiu a">
                        <span>{{ Number(voltage.voltageA.at(-1) || 0).toFixed(1) || 0}}<br>A</span>
                    </div>
                    <div class="qiu b">
                        <span>{{ Number(voltage.voltageB.at(-1) || 0).toFixed(1) }}<br>B</span>
                    </div>
                    <div class="qiu c">
                        <span>{{ Number(voltage.voltageC.at(-1) || 0).toFixed(1) }}<br>C</span>
                    </div>
                </div>
            </div>
        </div>
        <div>
            <div class="searchHead">
                <span class="name" @click="lineDialog = true">{{lineName}}</span>
                <LineDialog
                    @selectedLineNum="selectedLineNum"></LineDialog>
            </div>
            <div class="border lineData">
                <div class="currentPower">
                    <div class="charts">
                        <Charts 
                            :type="'current'" 
                            :simData="current" 
                            :xAxisData="xAxisLine"
                            ref="currentRef"
                            style="height: 100%; "></Charts>
                        <!-- <Charts 
                            :type="'currentFre'" 
                            :title="'频域'" 
                            :simData="currentFre" 
                            ref="currentFreRef"
                            style="height: 50%;"></Charts> -->
                    </div>
                    <div style="display: flex; flex-direction: column; justify-content: space-evenly;">
                        <span style="text-align: center;">电流(A)</span>
                        <div class="qiu a">
                            <span>{{ Number(current.currentA.at(-1) || 0).toFixed(1) }}<br>A</span>
                        </div>
                        <div class="qiu b">
                            <span>{{ Number(current.currentB.at(-1) || 0).toFixed(1) }}<br>B</span>
                        </div>
                        <div class="qiu c">
                            <span>{{ Number(current.currentC.at(-1) || 0).toFixed(1) }}<br>C</span>
                        </div>
                    </div>
                </div>
                <div class="currentPower">
                    <div class="charts">
                        <Charts
                            :type="'power'" 
                            :simData="power"
                            :xAxisData="xAxisLine"
                            ref="powerRef"
                            style="height: 100%;" ></Charts>
                        <!-- <Charts
                            :type="'powerFre'" 
                            :title="'频域'" 
                            :legend="['有功功率','无功功率']"
                            :simData="powerFre"
                            ref="powerFreRef"
                            style="height: 50%;" ></Charts> -->
                    </div>
                    <div style="display: flex; flex-direction: column; justify-content: space-evenly;">
                        <span style="text-align: center;">功率(MW)</span>
                        <div class="qiu a">
                            <span>{{ Number(power.powerActive.at(-1) || 0).toFixed(1) }}<br>有功</span>
                        </div>
                        <div class="qiu b">
                            <span>{{ Number(power.powerReactive.at(-1) || 0).toFixed(1) }}<br>无功</span>
                        </div>
                    </div>
                </div>
                
            </div>
            

        </div>
    </div>
</template>

<script setup>
import { onMounted, ref, watch, reactive, nextTick, useAttrs, inject,provide, onBeforeUnmount } from 'vue'
import { ConnectWebsocket } from "@/websocketConfig.js"
import { lineOptions } from '@/assets/geojson/options'
import Charts from './charts.vue'
import PointDialog from './PointDialog.vue'
import LineDialog from './LineDialog.vue'
import point from "@/assets/geojson/point.json"
import line from  "@/assets/geojson/line.json"
import { ElMessage } from 'element-plus'
import { Search } from '@element-plus/icons-vue'
import VoltageChart from './VoltageChart.vue'
const axios = require('axios').default;
let pointNum = inject('pointNum')
let lineNum = inject('lineNum')
let simTime = inject('simTime')
let client
let subscribes = []

let pointDialog = ref(false)
provide('pointDialog',pointDialog)
let lineDialog = ref(false)
provide('lineDialog',lineDialog)
let pointName = ref('')
let lineName = ref('')
let xAxisPoint = reactive([])
let xAxisLine = reactive([])
let voltageLevel = ref(500)
//时域
let voltageRef = ref()
let currentRef = ref()
let powerRef = ref()

let current = reactive({
    currentA:[],
    currentB:[],
    currentC:[],
})
let voltage = reactive({
    voltageA:[],
    voltageB:[],
    voltageC:[],
})
let power = reactive({
    powerActive:[],
    powerReactive:[],
})
//频域
let voltageFreRef = ref()
let currentFreRef = ref()
let powerFreRef = ref()

let currentFre = reactive({
    currentA:[],
    currentB:[],
    currentC:[],
})
let voltageFre = reactive({
    voltageA:[],
    voltageB:[],
    voltageC:[],
})
let powerFre = reactive({
    powerActive:[],
    powerReactive:[],
})
onMounted(() => {
    point.features.map(item => {
        if(pointNum.value == item.properties.nodeNum){
            pointName.value = item.properties.name
        }
    })
    line.features.map(item => {
        if(lineNum.value == item.properties.nodeNum){
            lineName.value = item.properties.name
        }
    })
    subscribeRightData()
})
onBeforeUnmount(() => {
    subscribes.map(item => {item.unsubscribe()})
})



function handlerData(data,listName,fn){
    if(data.status == 'SUCCESS'){
        if(data.nodeNum == pointNum.value || data.nodeNum == lineNum.value){
            fn(data[listName],data.times)
        }
    }else{
        ElMessage({
            message:'数据获取失败',
            type:'warning'
        })
    }
}

function searchPointData(data,times){
    voltage.voltageA = data.map(item => item.voltageA)
    voltage.voltageB = data.map(item => item.voltageB)
    voltage.voltageC = data.map(item => item.voltageC)
    xAxisPoint.length = 0
    times.map(item => {
        xAxisPoint.push(item)
    })
    voltageRef.value.setCharts()
    simTime.value = xAxisPoint.at(-1)
}
function searchLineData(data,times){
    current.currentA = data.map(item => item.currentA)
    current.currentB = data.map(item => item.currentB)
    current.currentC = data.map(item => item.currentC)
    power.powerActive = data.map(item => item.powerActive)
    power.powerReactive = data.map(item => item.powerReactive)

    xAxisLine.length = 0
    times.map(item => {
        xAxisLine.push(item)
    })
    powerRef.value.setCharts()
    currentRef.value.setCharts()
}
function receivePointData(data,times){
    xAxisPoint.push(times[0])
    data.map(item => {
        voltage.voltageA.push(item.voltageA)
        voltage.voltageB.push(item.voltageB)
        voltage.voltageC.push(item.voltageC)
        if(voltage.voltageA.length > 40){
            let voltageKeys = Object.keys(voltage)
            voltageKeys.map((item) => {
                voltage[item].shift()
            })
            xAxisPoint.shift()
        }
    })
    voltageRef.value.setCharts()
    simTime.value = xAxisPoint.at(-1)
}
function receiveLineData(data,times){
    xAxisLine.push(times[0])
    data.map(item => {
        current.currentA.push(item.currentA)
        current.currentB.push(item.currentB)
        current.currentC.push(item.currentC)
        power.powerActive.push(item.powerActive)
        power.powerReactive.push(item.powerReactive)

        if(current.currentA.length > 40){
            let currentKeys = Object.keys(current)
            currentKeys.map((item) => {
                current[item].shift()
            })
            
            let powerKeys = Object.keys(power)
            powerKeys.map((item) => {
                power[item].shift()
            })
            xAxisLine.shift()
        }
    })
    
    powerRef.value.setCharts()
    currentRef.value.setCharts()
}


function subscribeRightData(){
    let websocketService = new ConnectWebsocket()
    client = websocketService.client
    client.activate()
    client.onConnect = () => {
        //节点电压主动查询
        subscribes.push(client.subscribe('/user/response/getSsVol',(msg) => {
            handlerData(JSON.parse(msg.body),'ssVolResEntityList',searchPointData)
        }))
        //线路电流功率主动查询
        subscribes.push(client.subscribe('/user/response/getLineCtAndP',(msg) => {
            handlerData(JSON.parse(msg.body),'lineCtAndPResEntityList',searchLineData)
        }))
        //节点电压主动发送
        subscribes.push(client.subscribe('/response/nodeResult',(msg) => {
            handlerData(JSON.parse(msg.body),'ssVolResEntityList',receivePointData)
        }))
        //线路电流功率主动发送
        subscribes.push(client.subscribe('/response/lineResult',(msg) => {
            handlerData(JSON.parse(msg.body),'lineCtAndPResEntityList',receiveLineData)
        }))
        
        
        client.publish({
            destination:'/app/getSsVol',
            body:JSON.stringify({nodeNum:pointNum.value}),
        })
        client.publish({
            destination:'/app/getLineCtAndP',
            body:JSON.stringify({nodeNum:lineNum.value}),
        })
    }
}
function selectedPointNum(list){
    client.publish({
        destination:'/app/getSsVol',
        body:JSON.stringify({nodeNum:list.nodeNum}),
    })
    pointName.value = list.nodeNameStart
    voltageLevel.value = list.nodeVolLevel
}
function selectedLineNum(list){
    client.publish({
        destination:'/app/getLineCtAndP',
        body:JSON.stringify({nodeNum:list.nodeNum}),
    })
    lineName.value = list.nodeNameStart+'-'+list.nodeNameEnd
}

watch(lineNum,(newLine) => {
    line.features.map(item => {
        if(newLine == item.properties.nodeNum){
            lineName.value = item.properties.name
        }
    })
    client.publish({
        destination:'/app/getLineCtAndP',
        body:JSON.stringify({nodeNum:newLine}),
    })
})
watch(pointNum,(newPoint) => {
    point.features.map(item => {
        if(newPoint == item.properties.nodeNum){
            pointName.value = item.properties.name
        }
    })
    client.publish({
        destination:'/app/getSsVol',
        body:JSON.stringify({nodeNum:newPoint}),
    })
})

</script>

<style lang="less" scoped>

#rightData{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.searchHead{
    margin:1vh ;
    padding: 0 0 0.5vh 0;
    color: #fff;
    border-bottom: 0.2vh solid;
    border-image:linear-gradient(90deg,rgba(106,216,255,0) 0%,rgba(106,216,255,1) 25%,rgba(106,216,255,0) 100%) 1 1 1 1;
    font-size: 1.5vh;
    line-height: 3vh;
    height: 3vh;
}
.name{
    display: flex;
}
.name:hover{
    cursor: pointer;
}
.name::before{
    content:'';
    width: 2vh;
    height: 2vh;
    display: block;
    margin: auto 0;
    margin-right: 1vh;
    background-image: url(@/assets/img/搜索.svg);
    background-size: cover;
}

.currentPower{
    height: 50%; 
    width: 100%; 
    display: flex;
    justify-content: space-around;
}
.charts{
    height: 100%;
    width: 80%;
}

.qiu {
    position: relative;
    width: 6vh;
    height: 6vh;
    background: url(@/assets/img/cicle01.png) no-repeat center;
    background-size: 100%;
    display: table;
    span{
        display: table-cell; 
        vertical-align: middle; 
        text-align: center;
        text-overflow: ellipsis;
    }
}
.qiu::before {
    content:'';
    width: 6vh;
    height: 6vh;
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%,-50%);
    border-radius: 50%;
    box-shadow: rgba(106,216,255,1) 0 0 1vh inset;
    animation:scale 2s linear infinite;
}
.a{
    background: url(@/assets/img/cicle01.png) no-repeat center;
    background-size: auto 100%;
}
.a::before{
    content:'';
    width: 6vh;
    height: 6vh;
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%,-50%);
    border-radius: 50%;
    box-shadow: rgba(106,216,255,1) 0 0 1vh inset;
    animation:scale 2s linear infinite;
}
.b{
    background: url(@/assets/img/cicle02.png) no-repeat center;
    background-size:contain;
}
.b::before{
    content:'';
    width: 6vh;
    height: 6vh;
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%,-50%);
    border-radius: 50%;
    box-shadow: rgba(191,166,103, 1) 0 0 1vh inset;
    animation:scale 2s linear infinite;
}
.c{
    background: url(@/assets/img/cicle03.png) no-repeat center;
    background-size: auto 100%;
}
.c::before{
    content:'';
    width: 6vh;
    height: 6vh;
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%,-50%);
    border-radius: 50%;
    box-shadow: rgba(41,189,153, 1) 0 0 1vh inset;
    animation:scale 2s linear infinite;
}
@keyframes scale{
    0%{
        transform:translate(-50%,-50%) scale(1);
        opacity: 1;
    }
    100%{
        transform:translate(-50%,-50%) scale(1.5);
        opacity: 0;
    }
}
.pointData{
    position: relative;
    margin: 0 1vh;
    transition: all 1s;
    padding: 1vh;
    height: 25vh; 
    display: flex; 
    justify-content: space-evenly;
    font-size: 1vh;
    color:rgba(106,216,255,1);
}
.lineData{
    position: relative;
    margin: 0 1vh 1vh 1vh;
    transition: all 1s;
    padding: 1vh;
    height: 50vh; 
    font-size: 1vh;
    color:rgba(106,216,255,1);
}

.powerBoder{
    position: relative;
    display: flex;
    justify-content: space-evenly;
    margin: 2vh 1vh 1vh 1vh;
    height: 20vh;
    padding: 1vh 0 0 0;
    text-align: center;
    font-size: 1vh;
    color:rgba(106,216,255,1);
}

</style>
