<template>
    <el-dialog 
        id="lineDialog"
        v-model="lineDialog" 
        title="选择线路"
        width="40%"
        top="25vh">
        <template #header="{ close, titleId, titleClass }">
            <div class="my-header">
                <el-input
                    v-model="inputValue"
                    @clear="inputClear"
                    placeholder="搜索线路"
                    :prefix-icon="Search"
                    clearable
                    size="small"
                    />
                <el-button
                    size="small"
                    type="primary"
                    @click="searchLine">
                    搜索
                </el-button>
            </div>
        </template>
        <el-form 
            :model="form"
            size="small"
            :inline="true">
            <!-- <el-form-item label="">
                <el-radio-group 
                    v-model="form.headEnd" 
                    @change="change">
                    <el-radio label="首端" size="large">首端</el-radio>
                    <el-radio label="末端" size="large">末端</el-radio>
                </el-radio-group>
            </el-form-item> -->
            <el-form-item label="电压等级">
                <el-select 
                    v-model="form.nodeVolLevel" 
                    placeholder="电压等级"
                    :multiple="true"
                    :clearable="true"
                    :collapse-tags="true"
                    :collapse-tags-tooltip="true"
                    @change="change">
                    <el-option
                        v-for="item in voltages"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"    
                        ></el-option>
                </el-select>
                <el-checkbox
                    size="large" 
                    v-model="voltageChecked"
                    @change="voltageCheckedChange"
                    :indeterminate="voltageIndeterminate"/>
                
            </el-form-item>
            
            <el-form-item label="城市">
                <el-select 
                    v-model="form.city" 
                    placeholder="城市"
                    :multiple="true"
                    :clearable="true"
                    :collapse-tags="true"
                    :collapse-tags-tooltip="true"
                    @change="change">
                    <el-option
                        v-for="item in citys"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"    
                        ></el-option>
                </el-select>
                <el-checkbox
                    size="large" 
                    v-model="cityChecked"
                    @change="cityCheckedChange"
                    :indeterminate="cityIndeterminate"/>
            </el-form-item>
        </el-form>

      
        <el-table 
            :data="tableData" 
            height="25vh" 
            highlight-current-row
            @current-change="handleCurrentChange"
            style="width: 100%">
            <el-table-column prop="nodeNameStart" label="首端"/>
            <el-table-column prop="nodeNameEnd" label="末端"/>
            <el-table-column prop="nodeVolLevel" label="电压等级(kV)" />
            <el-table-column prop="city" label="城市" />
        </el-table>
        <template #footer>
            <span class="dialog-footer">
                <el-button @click="lineDialog = false">取消</el-button>
                <el-button type="primary" @click="sendLineNum">确定</el-button>
            </span>
        </template>
    </el-dialog>
</template>

<script setup>
import { ref, inject, reactive } from 'vue';
import { url } from '@/websocketConfig.js'
import { Search } from '@element-plus/icons-vue'
import axios from 'axios';
const emits = defineEmits(['selectedLineNum'])
let lineDialog = inject('lineDialog')
let lineNum = inject('lineNum')
let inputValue = ref('')
let currentLineList = {}
let form = reactive({
    nodeVolLevel:[],
    city:[],
    nodeNameEnd: "",
    nodeNameStart: ""
})
let tableData = reactive([])
let voltageChecked = ref(false)
let voltageIndeterminate = ref(false)
let cityChecked = ref(false)
let cityIndeterminate = ref(false)
let voltages = [
    {
        label:'220kV',
        value:220
    },
    {
        label:'500kV',
        value:500
    },
    {
        label:'1000kV',
        value:1000
    }
]
let citys = [
    {
        label:'杭州',
        value:'杭州'
    },
    {
        label:'宁波',
        value:'宁波'
    },
    {
        label:'嘉兴',
        value:'嘉兴'
    },
    {
        label:'湖州',
        value:'湖州'
    },
    {
        label:'金华',
        value:'金华'
    },
    {
        label:'衢州',
        value:'衢州'
    },
    {
        label:'温州',
        value:'温州'
    },
    {
        label:'台州',
        value:'台州'
    },
    {
        label:'丽水',
        value:'丽水'
    },
    {
        label:'舟山',
        value:'舟山'
    },
    {
        label:'绍兴',
        value:'绍兴'
    }
]


function change(value){
    voltageChecked.value = form.nodeVolLevel.length === voltages.length
    voltageIndeterminate.value = form.nodeVolLevel.length > 0 && form.nodeVolLevel.length < voltages.length
    cityChecked.value = form.city.length === citys.length
    cityIndeterminate.value = form.city.length > 0 && form.city.length < citys.length
    sendData()
}
function voltageCheckedChange(value){
    voltageIndeterminate.value = false
    value? form.nodeVolLevel = voltages.map(item => item.value) : form.nodeVolLevel = []
    sendData()
}

function cityCheckedChange(value){
    cityIndeterminate.value = false
    value? form.city = citys.map(item => item.value) : form.city = []
    sendData()
}



function sendData(){
    axios.post(url+'/getNodeInfoLine',form).
        then((res) => {
            tableData.length = 0
            res.data.nodeInfoEntityList.map(item => {
                tableData.push(item)
            })
        })
}

function searchLine(){
    axios.post(url+'/getNodeInfoLine1',{name:inputValue.value}).
        then((res) => {
            tableData.length = 0
            res.data.nodeInfoEntityList.map(item => {
                tableData.push(item)
            })
        })
}
function inputClear(){
    sendData()
}
function handleCurrentChange(value){
    currentLineList = value
}
function sendLineNum(){
    lineDialog.value = false
    lineNum.value = currentLineList.nodeNum
    emits('selectedLineNum',currentLineList)
}

</script>
<style lang="less">

#lineDialog  .el-form-item{
    margin-bottom: 0;
    width: 48%;
}
#lineDialog .el-select{
    width: 90%;
}




</style>
