import { createRouter,createWebHashHistory } from "vue-router"
import Login from "../components/Login.vue"
import MapContainer from "../components/map/MapContainer.vue"
import Home from "../components/home/Home.vue"
import DataAnalysis from "../components/dataAnalysis/DataAnalysis.vue"
import MainNetwork from "../components/dataContainer/mainNetwork/MainNetwork.vue"
import DistributionNetwork from "../components/dataContainer/distributionNetwork/DistributionNetwork.vue"
let routes = [
    {
        path:'/',
        redirect:'/login',
    },
    {
        path:'/login',
        component:Login,
        name:'login',
        meta:{required:false}
    },
    {
        path:'/home',
        component:Home,
        name:'home',
        meta:{required:true}
    },
    {
        path:'/map',
        component:MapContainer,
        name:'map',
        redirect:'/map/MainNetwork',
        children:[
            {
                path:'MainNetwork',
                name:'MainNetwork',
                component:MainNetwork
            },
            {
                path:'DistributionNetwork',
                name:'DistributionNetwork',
                component:DistributionNetwork
            }
        ]
    },
    {
        path:'/dataAnalysis',
        component:DataAnalysis,
        name:'dataAnalysis'
    }
]
let router = createRouter({
    history:createWebHashHistory(),
    routes:routes
})
window.isLogin = false
