<template>
    <div id="settingDialog">
        <el-dialog 
            v-model="settingDialog" 
            title="薄弱环节诊断配置"
            width="25%"
            top="25vh">
            <template #header="{ close, titleId, titleClass }">
            
            </template>
            <el-form
                label-position="left"
                label-width="120px"
                size="small"
                :hide-required-asterisk="true"
                :model="settingData">
                <el-form-item label="变电站负载率(%)" :rules="rule" prop="nodeFaultValue">
                    <el-input v-model.number="settingData.nodeFaultValue" />
                </el-form-item>
                <el-form-item label="线路负载率(%)" :rules="rule" prop="lineFaultValue">
                    <el-input v-model.number="settingData.lineFaultValue" />
                </el-form-item>
                <el-form-item label="1000kV变电站(kV)">
                    <el-form-item :rules="rule" prop="volFaultValueMin1000">
                        <el-input v-model.number="settingData.volFaultValueMin1000" />
                    </el-form-item>
                    -
                    <el-form-item :rules="rule" prop="volFaultValueMax1000">
                        <el-input v-model.number="settingData.volFaultValueMax1000" />
                    </el-form-item>
                </el-form-item>
                <el-form-item label="500kV变电站(kV)">
                    <el-form-item :rules="rule" prop="volFaultValueMin500">
                        <el-input v-model.number="settingData.volFaultValueMin500" />
                    </el-form-item>
                    -
                    <el-form-item :rules="rule" prop="volFaultValueMax500">
                        <el-input v-model.number="settingData.volFaultValueMax500" />
                    </el-form-item>
                </el-form-item>
                <el-form-item label="220kV变电站(kV)">
                    <el-form-item :rules="rule" prop="volFaultValueMin220">
                        <el-input v-model.number="settingData.volFaultValueMin220" />
                    </el-form-item>
                    -
                    <el-form-item :rules="rule" prop="volFaultValueMax220">
                        <el-input v-model.number="settingData.volFaultValueMax220" />
                    </el-form-item>
                </el-form-item>
                <el-form-item label="频率(Hz)">
                    <el-form-item :rules="rule" prop="freqFaultValueMin">
                        <el-input v-model.number="settingData.freqFaultValueMin" />
                    </el-form-item>
                    -
                    <el-form-item :rules="rule" prop="freqFaultValueMax">
                        <el-input v-model.number="settingData.freqFaultValueMax" />
                    </el-form-item>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="settingDialog = false">取消</el-button>
                    <el-button type="primary" @click="setData">提交</el-button>
                </span>
            </template>
        </el-dialog>
    </div>
    

</template>

<script setup>
import { ref, inject, reactive, onMounted } from 'vue';
import { url } from '@/websocketConfig';
import axios from 'axios';
let settingDialog = inject('settingDialog')
let settingData = reactive({
    nodeFaultValue:0,
    lineFaultValue:1,
    volFaultValueMax1000:2,
    volFaultValueMin1000:3,
    volFaultValueMax500:4,
    volFaultValueMin500:5,
    volFaultValueMax220:6,
    volFaultValueMin220:7,
    freqFaultValueMax:8,
    freqFaultValueMin:9,
})
let rule =ref([
        { required: true, message: '请输入数字' },
        { type: 'number', message: '请输入数字' },
      ])

onMounted(() => {
    getData()
})
function setData(){
    console.log(111)
    axios.post(url+'/setFaultsConfig',settingData).then((res) => {
        if(res.data.status == "FAILED"){
            ElMessage.error('设置失败')
        }
    })
    settingDialog.value = false
}
function getData(){
    axios.post(url+'/getFaultsConfig',settingData).then((res) => {
        if(res.data.status == "FAILED"){
            ElMessage.error('获取配置信息失败')
        }else if(res.data.status == "SUCCESS"){
            Object.assign(settingData,res.data.faultsStandardEntity)
        }
    })
}
</script>
<style lang="less">
#settingDialog .el-dialog__header{
    padding-left:2vh;
    margin-bottom: 1vh;
}
#settingDialog .el-dialog__body{
    padding: 0 2vh;
}
#settingDialog .el-dialog__footer{
    padding:0 2vh;
    padding-bottom: 1.5vh;
}


</style>