<template>
    <div id="voltageMonitor">
        <!-- <div>
            <span>频率&nbsp;&nbsp;<span class="numData">{{ 50 }}</span>&nbsp;&nbsp;Hz</span>
        </div>
        <div>
            <span>吴宁&nbsp;&nbsp;<span class="numData">{{ 220 }}</span>&nbsp;&nbsp;kV</span>
        </div>
        <div>
            <span>吴宁&nbsp;&nbsp;<span class="numData">{{ 500 }}</span>&nbsp;&nbsp;kV</span>
        </div>
        <div>
            <span>吴宁&nbsp;&nbsp;<span class="numData">{{ 1000 }}</span>&nbsp;&nbsp;kV</span>
        </div> -->
        
        <div id="frequency"></div>
        <div id="voltage220"></div>
        <div id="voltage500"></div>
        <div id="voltage1000"></div>
    </div>
        
</template>

<script setup>
import * as echarts from 'echarts'
import { ref, reactive, onMounted, defineExpose, inject } from 'vue'
import { fontSize } from '@/util/util.js'
let pointNum = inject('pointNum')
let props = defineProps({
    freVoltage:{
      type:Object
    }
})

let colors = ['#6ad8ff','#FFC56C', 'rgba(0,255,127, 1)','#a6d8ff']

let voltage220Chart
let voltage500Chart
let voltage1000Chart
let frequencyChart

let options = {
    title:{
      right:0,
      top:0,
      textStyle:{
        color:colors[0],
        fontSize:fontSize(10),
        fontWeight:'normal'
      }
    },
    xAxis: {
      type: 'category',
      axisTick:{
        show:false
      },
      axisLine:{
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      splitLine:{
        show:false
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
    },
    yAxis: {
      type: 'value',
      splitLine:{
        show:false
      },
      axisLine:{
        show:true,
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
      splitNumber:2,
    },
    legend:{
        icon: 'circle',
        top:0,
        itemWidth:fontSize(10),
        textStyle: {
            color: colors[0],
            fontSize:fontSize(10)
        },
    },
    tooltip:{
        show:true,
        trigger:'axis',
        snap:true,
        axisPointer:{
          type:'line',
        },
        enterable:true,
        triggerOn:'click'
    },
    
    series: [
      {
        type: 'line',
        smooth: true,
        symbol:"none",
        itemStyle:{
          color:colors[0]
        },
        lineStyle:{
          width:fontSize(2)
        },
      }
    ],
    grid:{
      show:false,
      left:'15%',
      right:'5%',
      top:'20%',
      bottom:'25%',
      // containLabel: true
    }
}

onMounted(() => {
    window.setPointNum = setPointNum
    
    let voltage220Option = JSON.parse(JSON.stringify(options)) 
    let voltage500Option = JSON.parse(JSON.stringify(options)) 
    let voltage1000Option = JSON.parse(JSON.stringify(options)) 
    let frequencyOption = JSON.parse(JSON.stringify(options)) 


    voltage220Option.tooltip.formatter = setToolTip
    voltage500Option.tooltip.formatter = setToolTip
    voltage1000Option.tooltip.formatter = setToolTip

    voltage220Option.title['text'] = '电压（220kV）'
    voltage500Option.title['text'] = '电压（500kV）'
    voltage1000Option.title['text'] = '电压（1000kV）'
    frequencyOption.title['text'] = '频率'

 
    frequencyOption.yAxis['min'] = setMin
    voltage220Option.yAxis['min'] = setMin
    voltage500Option.yAxis['min'] = setMin
    voltage1000Option.yAxis['min'] = setMin

    frequencyOption.yAxis['max'] = setMax
    voltage220Option.yAxis['max'] = setMax
    voltage500Option.yAxis['max'] = setMax
    voltage1000Option.yAxis['max'] = setMax


    voltage220Chart = echarts.init(document.getElementById('voltage220'));
    voltage220Chart.setOption(voltage220Option);
    voltage500Chart = echarts.init(document.getElementById('voltage500'));
    voltage500Chart.setOption(voltage500Option);
    voltage1000Chart = echarts.init(document.getElementById('voltage1000'));
    voltage1000Chart.setOption(voltage1000Option);
    frequencyChart = echarts.init(document.getElementById('frequency'));
    frequencyChart.setOption(frequencyOption);

    window.addEventListener('resize',() => {
        voltage220Chart.resize()
        voltage500Chart.resize()
        voltage1000Chart.resize()
        frequencyChart.resize()
    })
    setCharts()
})

function setCharts(){
  console.log(props);
    frequencyChart.setOption({
      xAxis:{
        data:props.freVoltage.xAxisData
      },
      series:[
        {
          type:'line',
          data:props.freVoltage.freq,
        }
      ]
    })
    voltage220Chart.setOption({
      xAxis:{
        data:props.freVoltage.xAxisData
      },
      series:[
        {
          type:'line',
          data:props.freVoltage.voltage220,
        }
      ]
    });
    voltage500Chart.setOption({
      xAxis:{
        data:props.freVoltage.xAxisData
      },
      series:[
        {
          type:'line',
          data:props.freVoltage.voltage500,
        }
      ]
    });
    voltage1000Chart.setOption({
      xAxis:{
        data:props.freVoltage.xAxisData
      },
      series:[
        {
          type:'line',
          data:props.freVoltage.voltage1000,
        }
      ]
    });
}

function setToolTip(params){
  let str = ` <div>${params[0].name}</div>
              <div class='toolTipItem' onclick="window.setPointNum('${params[0].data.nodeNum}')">${params[0].marker+params[0].data.value}kV&nbsp&nbsp&nbsp&nbsp${params[0].data.nodeName}</div>`
  return str
}

function setMax(value){
    return parseInt(value.max + 5)
}
function setMin(value){
    return parseInt(value.min - 5)
}
function setPointNum(value){
    pointNum.value = value
    console.log(value)
}
defineExpose({
    setCharts
})




</script>

<style>
#voltageMonitor{
    height: 16vh;
    display: grid;
    grid-template-columns: repeat(2 ,1fr);
    grid-template-rows: repeat(2 ,1fr);
    font-size: 1vh;
    text-align: center;
    align-items: center;
    color: #fff;
}
#voltageMonitor > div:nth-child(-n+4){
    width: 100%;
    height: 100%;
}
/* #voltageMonitor > div:nth-child(1) {
    grid-column: 1/3;
}
#voltageMonitor > div:nth-child(2) {
    grid-column: 3/5;
}
#voltageMonitor > div:nth-child(3) {
    grid-column: 1/3;
}
#voltageMonitor > div:nth-child(4) {
    grid-column: 3/5;
} */
.numData{
    font-size: 1.8vh;
    color: #6ad8ff;
}
</style>