<template>
    <div id="gridScale">
        
        <div id="gridCapacity"></div>
        <div id="installCapacity"></div>
    </div>
</template>

<script setup>
import { onMounted, defineExpose,ref } from 'vue';
import * as echarts from 'echarts';
import { fontSize } from '@/util/util.js'
import axios from 'axios';
import { ElMessage } from 'element-plus';
import { url } from '@/websocketConfig.js';
let gridCapacity = [
    { value: 1800, name: '1000kV' },
    { value: 12265, name: '500kV' },
    { value: 16064, name: '220kV' },
    { value: 15096, name: '110kV' }
]
let installCapacity = [
    { value: 63580, name: '火电' },
    { value: 11710, name: '水电' },
    { value: 15170, name: '光伏' },
    { value: 9110, name: '核电' },
    { value: 10960, name: '风电' },
]
let colors = ["#6ad8ff", "#865858", "#4be4c5", "#c8f6ed",'#737373','#F2E8C4','#f2b632']
let options = {
    color: colors,
    grid:{
        top:'middle',
        left:'0',
        right:"50%",
        containLabel: true,
        height:'100%'
    },
    title:{
        text:'电网规模',
        left:'center',
        bottom:-5,
        textStyle:{
            color:colors[0],
            fontSize:fontSize(12),
            fontWeight:'normal'
        }
    },
    legend:{
        show:true,
        top:'center',
        right:0,
        icon:'circle',
        itemWidth:fontSize(10),
        itemHeight:fontSize(8),
        itemGap:fontSize(5),
        orient: 'vertical',
        textStyle:{
            color:'#a6d8ff',
            padding:[0,0,-2,0],
            rich: {
                a: {
                    verticalAlign: 'middle',
                },
            },
            fontSize:fontSize(10)
        },
    },
    tooltip:{
        show:true,
        trigger:'item',
        confine:true
    },
    xAxis: {
        type: 'value',
        axisTick:{
            show:false
        },
        splitLine:{
            show:false
        },
        axisLine:{
            show:false
        },
        axisLabel:{
            show:false
        }
    },
    yAxis: {
        type: 'category',
        axisTick:{
            show:false
        },
        splitLine:{
            show:false
        },
        axisLine:{
            show:false
        },
        axisLabel:{
            show:false
        }
    },
    series:[]
};


onMounted(() => {
    getData()
    
})

function getData(){
    axios.post(url+'/getGridInfo',{
        }).then((res) => {
            if(res.data.status != "SUCCESS"){
                ElMessage.error('电网规模数据获取失败')
            }else{
                gridCapacity = res.data.gridCapacity
                installCapacity = res.data.installCapacity
                initChart()
            }
        })
}

function initChart(){
    let gridOptions = JSON.parse(JSON.stringify(options))
    let installOptions = JSON.parse(JSON.stringify(options))
    gridCapacity.map(item => {
        gridOptions.series.push(
            {
                type:'bar',
                data:[item.value],
                name:item.name,
                barWidth:fontSize(8),
                barGap:fontSize(0.3),
                label:{
                    show:true,
                    color:'#fff',
                    position:'right',
                    fontSize:fontSize(8)
                }
            }
        )
    })
    installCapacity.map(item => {
        installOptions.series.push(
            {
                type:'bar',
                data:[item.value],
                name:item.name,
                barWidth:fontSize(8),
                barGap:fontSize(0.3),
                label:{
                    show:true,
                    color:'#fff',
                    position:'right',
                    fontSize:fontSize(8)
                }
            }
        )
    })

    gridOptions.title.text = '主变容量（MVA）'
    installOptions.title.text = '装机容量（MW）'

    gridOptions.tooltip.formatter = setToolTip
    installOptions.tooltip.formatter = setToolTip
    
    let gridChart = echarts.init(document.getElementById('gridCapacity'));
    gridChart.setOption(gridOptions);
    let installChart = echarts.init(document.getElementById('installCapacity'));
    installChart.setOption(installOptions);
    window.addEventListener('resize',() => {
        gridChart.resize()
        installChart.resize()
    })
}


function setToolTip(params){
    let str = `${params.marker}&nbsp;&nbsp;${params.seriesName}&nbsp;&nbsp;${params.data}`
    return str
}


</script>

<style lang="less" scoped>
#gridScale{
    height: 9vh;
    display: flex;
    div{
        width: 50%;
        height: 100%;
    }
}
</style>