<template>
    <div id="voltage"></div>
</template>

<script setup>
import * as echarts from 'echarts'
import { ref, reactive, onMounted,defineExpose } from 'vue'
import { fontSize } from '@/util/util.js'

let props = defineProps({
        voltageLevel:{
            type:Number
        },
        simData:{
            type:Object
        },
        xAxisData:{
            type:Array
        }
    })
let colors = ['#6ad8ff','#FFC56C', 'rgba(0,255,127, 1)','#a6d8ff']

let chart

let options = {
    title:{
      left:0,
      textStyle:{
        color:colors[0],
        fontSize:fontSize(10),
        fontWeight:'normal'
      }
    },
    // grid:{
    //   show:false,
    //   left:'10%',
    //   right:'0%',
    //   top:'10%',
    //   bottom:'10%',
    // },
    xAxis: {
      type: 'category',
      axisTick:{
        show:false
      },
      axisLine:{
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      splitLine:{
        show:false
      },
      axisLabel:{
        fontSize:fontSize(10),
        interval:18
      },
    },
    yAxis: {
      type: 'value',
      splitLine:{
        show:false
      },
      axisLine:{
        show:true,
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
      splitNumber:2,
      max:function(value){
        return parseInt(value.max + 5) 
      },
      min:function(value){
        return  parseInt(value.min - 5) 
      },
    },
    legend:{
        icon: 'circle',
        top:0,
        itemWidth:fontSize(10),
        textStyle: {
            color: colors[0],
            fontSize:fontSize(10)
        },
    },
    tooltip:{
        show:true,
        trigger:'axis',
        snap:true,
        axisPointer:{
          type:'line',
        }
    },
    
    series: [],
}
onMounted(() => {
    let legend = ['A相','B相','C相']
    options.title.text = `电压(${props.voltageLevel}kV)`
    legend.map((item,index) => {
       options.series.push({
          name: item,
          type: 'line',
          smooth: true,
          symbol:"none",
          itemStyle:{
            color:colors[index]
          },
          lineStyle:{
            width:fontSize(2)
          },
        })
    })
    chart = echarts.init(document.getElementById("voltage"));
    chart.setOption(options);
    window.addEventListener('resize',() => {
      chart.resize()
    })
})
function setCharts(){
    options.title.text = `电压(${props.voltageLevel}kV)`
    // let minPrecent = 0.1
    // let maxPrecent = 0.1
    // switch(props.voltageLevel){
    //     case 220:
    //         minPrecent = 0.1
    //         maxPrecent = 0.1
    //         break;
    //     case 500:
    //         minPrecent = 0.05
    //         maxPrecent = 0.05
    //         break;
    //     case 1000:
    //         minPrecent = 0.01
    //         maxPrecent = 0.01
    //         break;
    // }
    // options.yAxis['max'] = props.voltageLevel*(1 + maxPrecent)
    let keys = Object.keys(props.simData)
    keys.map((key,index) => {
      options.series[index].data = props.simData[key]
    })

    options.xAxis.data = props.xAxisData
    chart.setOption(options);
}

defineExpose({
  setCharts
})




</script>

