<template>
    <div style="height: 100vh;width:100vw;position: relative;">
        <div id="mapContainer">
        </div>
        <Legend @tranArray="tranArray"></Legend>
        <DataDisplay></DataDisplay>
    </div>
</template>

<script setup>
import AMapLoader from "@amap/amap-jsapi-loader"
import { h, ref, watch, onMounted, onBeforeUnmount, nextTick, provide,reactive, createApp, render,createVNode} from "vue"
import {ElNotification,ElMessage} from "element-plus"
import DataDisplay from '../dataContainer/DataDisplay.vue'
import Legend from "./legend.vue"
import InfoWindow from "./InfoWindow.vue"
import { fontSize } from "@/util/util.js"
import point from "../../assets/geojson/point.json"
import line from  "../../assets/geojson/line.json"
import tran220 from "../../assets/img/220变电站.png"
import tran500 from "../../assets/img/500变电站.png"
import tran1000 from "../../assets/img/substation1000.png"
import choushui from "../../assets/img/抽水蓄能电站.png"
import chuneng from "../../assets/img/储能站.png"
import tiaoxiangji from "../../assets/img/调相机.png"
import fengdian from "../../assets/img/风电站.png"
import guangfu from "../../assets/img/光伏电站.png"
import hedian from "../../assets/img/核电站.png"
import huanliu from "../../assets/img/换流站.png"
import huodian from "../../assets/img/火电站.png"
import qianyin from "../../assets/img/牵引站.png"
import shuidian from "../../assets/img/水电站.png"
import positionIcon from "../../assets/img/定位.svg"
import { ConnectWebsocket } from "../../websocketConfig.js"
import { linePosition} from '@/assets/geojson/options.js'
window._AMapSecurityConfig = {
  securityJsCode:'b99d79117716a14dc833185702829854',
}

let client
let currentPoint
let currentLine
let map
let subscribes = []
let pointNum = ref('dev132068')
let lineNum = ref('dev051007')
provide('pointNum',pointNum)
provide('lineNum',lineNum)
let pointLoadRate = reactive([])
let lineLoadRate = reactive([])
let pointLayer
let lineLayer
let clickMap  = false
onMounted(() => {
    subscribeLoadRate()
})
onBeforeUnmount(() => {
    subscribes.map(item => item.unsubscribe())
})
let voltageLevels = [500]
function tranArray(voltage){
    voltageLevels = voltage
    setLineStyle(voltage)
    setPointStyle(voltage)
}
let bound = 60
AMapLoader.load({
        "key": "8678ea7c453b7244ee6ab87e0137dfa1",             
        "version": "2.0",  
        "plugins": ["AMap.Scale","AMap.DistrictSearch","AMap.PolylineEditor","AMap.infoWindow"],  
        "Loca":{              
          "version": '2.0.0'  
        },
    }).then((AMap)=>{
            map = new AMap.Map('mapContainer',{
                viewMode:"3D",    
                zoom:9,           
                mapStyle: 'amap://styles/ab4a9c75ce8d68ca74b18c2628c7d638',
                doubleClickZoom:false,
                // whitesmoke，grey
                // mapStyle:'amap://styles/whitesmoke',
                center:[120.423314,29.548723], 
                features: ['bg', 'building'], //'bg', 'road', 'building', 'point'
                // terrain:true,//展示地形
                // showLabel:false //隐藏地图文字
                isHotspot:false,
                // pitch: 40,
            });
            let loca = new Loca.Container({
                map:map
            })
            let pointSource = new Loca.GeoJSONSource({
                data:point
            })
            pointLayer = new Loca.LabelsLayer({
                loca:loca,
                collision:true
            })
            pointLayer.setSource(pointSource);
            
            pointLayer.setStyle({
                rank:(e,feat) => {
                    switch(feat.properties.voltage){
                            case 500: return 2;
                            case 1000: return 3;
                            case 220: return 1;
                            default: return 1
                        }
                },
                icon:{
                    image:(e,feat) => {
                        if(voltageLevels.includes(feat.properties.voltage)){
                            switch(feat.properties.type){
                                case '220': return tran220;
                                case '500': return tran500;
                                case '1000': return tran1000;
                                case "牵引站": return qianyin;
                                case "换流站": return huanliu;
                                case "火电站": return huodian;
                                case "风电站": return fengdian;
                                case "储能站": return chuneng;
                                case "光伏电站": return guangfu;
                                case "抽水蓄能电站": return choushui;
                                case "核电站": return hedian;
                                case "水电站": return shuidian;
                                case "调相机": return tiaoxiangji;
                                default: return tran220
                            }
                        }else{
                            return 
                        }
                    },
                    size:(e,feat) => {
                        let array = ['500','220','1000','调相机',"换流站"]
                        if(array.includes(feat.properties.type)){
                            switch(feat.properties.voltage){
                                case 220: return [fontSize(20),fontSize(20)];
                                case 500: return [fontSize(30),fontSize(30)];
                                case 1000: return [fontSize(40),fontSize(40)];
                            }
                        }else{
                            return [fontSize(20),fontSize(13)];
                        }
                        
                    },
                    anchor:'center'
                },
                text: {
                    content: (e,feat) => {
                        if(voltageLevels.includes(feat.properties.voltage)){
                            return `${feat.properties.name}(${100}%)`
                        }else{
                            return 
                        }
                    },
                    direction: 'right',
                    style: {
                        fontSize: fontSize(12),
                        fillColor: '#fff',
                    }
                },
                extData:{
                    nodeNum:(e,feat) => {
                        return feat.properties.nodeNum
                    },
                    voltage:(e,feat) => {
                        return feat.properties.voltage
                    }
                },
            })

           
           
            let lineSource = new Loca.GeoJSONSource({
                data:line
            })
            lineLayer = new Loca.LineLayer({
                loca,
                zIndex: 10,
                opacity: 1,
                visible: true,
            });
            lineLayer.setSource(lineSource)
            lineLayer.setStyle({
                lineWidth:(e,feat) => {
                    if(feat.properties.nodeNum == lineNum.value){
                        return fontSize(5)
                    }else{
                        switch(feat.properties.voltage){
                            case 220: return fontSize(1.5)
                            case 500:  return fontSize(3)
                            case 1000:  return fontSize(5)
                            case "500(直流)": return fontSize(3);
                        }
                    }
                },
                color:(e,feat) => {
                    if(voltageLevels.includes(feat.properties.voltage)){
                        return 'rgba(106,216,255,0.7)'
                    }else{
                        return 'rgba(106,216,255,0)'
                    }
                }
            })

            
           
            
            currentPoint = new AMap.Marker({
                map:map,
                position:[120.423314,29.548723],
                icon:new AMap.Icon({
                    image:positionIcon,
                    // size:[fontSize(25),fontSize(30)],
                    imageSize:[fontSize(25),fontSize(30)]
                }),
                anchor:'bottom-center'
            })
            map.add(currentPoint)
            let text,linePosition
            line.features.map(item => {
                if(item.properties.nodeNum == lineNum.value){
                    text = item.properties.name
                    linePosition = item.geometry.coordinates[0]
                }
            })
            currentLine = new AMap.Text({
                position: linePosition,
                anchor: 'bottom-center',
                text: text,
                extData:{
                    name:text
                },
                style: {'background-color':'transparent','color':'#0ff','border':'none','font-size':`${fontSize(12)}px`},
            })
            map.add(currentLine);


            var content = [
                "<div id='test'></div>"
            ];
            
            let infoWindow = new AMap.InfoWindow({
                isCustom:true,
                offset: new AMap.Pixel(16, -45),
                content:content
            })
            

            pointLayer.on('complete',function(){
                let labelMarkers = pointLayer.getLabelsLayer().getAllOverlays();
                for (const marker of labelMarkers) {
                    marker.on('click',() => {
                        if(!voltageLevels.includes(marker.getExtData().voltage)) return
                        clickMap = true
                        pointNum.value = marker.getExtData().nodeNum
                        currentPoint.setPosition(marker.getOptions().position)
                        
                        infoWindow.open(map,marker.getPosition())
                        const instance = createVNode(InfoWindow,{infoWindowData:'111'})
                        render(instance,document.querySelector("body"))
                        let dom = instance.el
                        console.log(dom)
                        infoWindow.setContent(dom)
                    })
                }
            })
            var path = [
                [116.453322, 39.920255],
                [116.460703, 39.897555],
                [116.452292, 39.892353],
                [116.439846, 39.891365]
            ]

            var polyline1 = new AMap.Polyline({
            path: path,
            strokeColor: "#FF33FF",
            strokeWeight: 6,
            strokeOpacity: 0.9,
            zIndex: 50,
            bubble: true,
            })
            map.add([polyline1])
            var polyEditor = new AMap.PolylineEditor(map);
            
            let contextMenu = new AMap.ContextMenu();
            contextMenu.addItem('编辑',() => {
                polyEditor.setTarget(polyline1)
                polyEditor.open()
            })
            currentPoint.on('rightclick',(e) => {
                contextMenu.open(map,e.lnglat)
            })



            map.on('click',(e) => {
                const lineFeature = lineLayer.queryFeature(e.pixel.toArray());
                console.log(e);
                if(lineFeature && voltageLevels.includes(lineFeature.properties.voltage)){
                    clickMap = true
                    lineNum.value = lineFeature.properties.nodeNum;
                    currentLine.setPosition([e.lnglat.lng,e.lnglat.lat])
                }
            })
            loca.animate.start();
            
            map.on('mousemove',(e)=>{
                requestAnimationFrame(() => {
                    const lineFeature = lineLayer.queryFeature(e.pixel.toArray());
                    map.setDefaultCursor('default')
                    if(lineFeature){
                        map.setDefaultCursor('pointer')
                    }
                })
            })

    }).catch((e)=>{
        console.log(e);  
    });

function lineClick(e,lineLayer){
    const lineFeature = lineLayer.queryFeature(e.pixel.toArray());
    if(lineFeature){
        clickMap = true
        lineNum.value = lineFeature.properties.nodeNum;
        currentLine.setPosition([e.lnglat.lng,e.lnglat.lat])
        var polyEditor = new AMap.PolylineEditor(map, polyline1);
    }
}


function subscribeLoadRate(){
    let websocketService = new ConnectWebsocket()
    client = websocketService.client
    client.activate()
    client.onConnect = () => {
        subscribes.push(client.subscribe('/response/getLoadRate',(msg) => {
            handlePointLoadRate(JSON.parse(msg.body))
            handleLineLoadRate(JSON.parse(msg.body))
        }))
        setTimeout(() => {
            client.publish({
                destination:'/app/getLoadRate',
                body:'',
            })

        },1000)
        
    }
}

function handlePointLoadRate(data){
    pointLoadRate = data.loadRateEntityList
    setPointStyle(voltageLevels)
}
function handleLineLoadRate(data){
    lineLoadRate = data.loadRateEntityList
    setLineStyle(voltageLevels)
}
function setLineStyle(levels){
    lineLayer.setStyle({
        lineWidth:(e,feat) => {
            if(feat.properties.nodeNum == lineNum.value){
                return fontSize(6)
            }else{
                switch(feat.properties.voltage){
                    case 220: return fontSize(2)
                    case 500:  return fontSize(3)
                    case 1000:  return fontSize(5)
                    case "500(直流)": return fontSize(3);
                }
            }
        },
        color:(e,feat) => {
            if(!levels.includes(feat.properties.voltage)) return 'rgba(255,0,0,0)'
            if(feat.properties.nodeNum == 'nodenum') return '#969696'
            let color = '#00f'
            pointLoadRate.map((item) => {
                if(feat.properties.nodeNum != item.nodeNum) return
                switch(true){
                    case item.loadRate >= bound: return color = '#ff0000'
                    case item.loadRate < bound: return color = '#6ad8ff'
                }
            })
            return color
        }
    })
}
function setPointStyle(levels){
    pointLayer.setStyle({
        rank:(e,feat) => {
            let rank = 0
            if(feat.properties.loadRate){
                pointLoadRate.map((item) => {
                    if(feat.properties.nodeNum == item.nodeNum){
                        switch(true){
                            case item.loadRate >= bound: return rank = 4;
                            case item.loadRate < bound: return rank = 2;
                        }
                    }
                })
                switch(feat.properties.voltage){
                    case 1000: return rank = 6
                    case 500: return rank = 5
                    case 220: return rank = 2
                }
            }
            return rank
        },
        text: {
            content: (e,feat) => {
                if(!feat.properties.loadRate) return feat.properties.name
                let name, loadRate
                pointLoadRate.map((item) => {
                    if(feat.properties.nodeNum != item.nodeNum) return
                    name = feat.properties.name
                    loadRate = item.loadRate
                })
                return `${name}(${loadRate}%)`
            },
            direction: 'right',
            style: {
                fontSize: fontSize(12),
                fillColor: (e,feat) => {
                    if(!levels.includes(feat.properties.voltage)) return 'rgba(0,0,0,0)'
                    if(feat.properties.nodeNum == 'nodenum') return '#969696'
                    if(!feat.properties.loadRate) return '#fff'
                    let color = '#00f'
                    pointLoadRate.map((item) => {
                        if(feat.properties.nodeNum != item.nodeNum) return
                        switch(true){
                            case item.loadRate >= bound: return color = '#F00';
                            case item.loadRate < bound: return color = '#4EEE94';
                        }
                    })
                    return color
                },
            }
        },
        icon:{
            image:(e,feat) => {
                if(voltageLevels.includes(feat.properties.voltage)){
                    switch(feat.properties.type){
                        case '220': return tran220;
                        case '500': return tran500;
                        case '1000': return tran1000;
                        case "牵引站": return qianyin;
                        case "换流站": return huanliu;
                        case "火电站": return huodian;
                        case "风电站": return fengdian;
                        case "储能站": return chuneng;
                        case "光伏电站": return guangfu;
                        case "抽水蓄能电站": return choushui;
                        case "核电站": return hedian;
                        case "水电站": return shuidian;
                        case "调相机": return tiaoxiangji;
                        default: return tran220
                    }
                }else{
                    return 
                }
            },
            size:(e,feat) => {
                let array = ['500','220','1000','调相机',"换流站"]
                if(array.includes(feat.properties.type)){
                    switch(feat.properties.voltage){
                        case 220: return [fontSize(20),fontSize(20)];
                        case 500: return [fontSize(30),fontSize(30)];
                        case 1000: return [fontSize(40),fontSize(40)];
                    }
                }else{
                    return [fontSize(20),fontSize(13)];
                }
                
            },
            anchor:'center'
        },
    })
}
watch(pointNum, (newNode) => {
    point.features.map((feat) => {
        if(feat.properties.nodeNum == newNode){
            currentPoint.setPosition(feat.geometry.coordinates)
            if(!clickMap){
                map.setCenter(feat.geometry.coordinates,false,500)
            }
        }
    })
    clickMap = false
})
watch(lineNum, (newLine) => {
    line.features.map((feat) => {
        if(feat.properties.nodeNum == newLine){
            currentLine.setText(feat.properties.name)
            currentLine.setExtData({name:feat.properties.name})
            if(!clickMap){
                map.setCenter(feat.geometry.coordinates[0],false,500)
                currentLine.setPosition(feat.geometry.coordinates[0])
            }
        }
    })
    setLineStyle(voltageLevels)
    clickMap = false
})


</script>

<style lang="less" scoped>
#mapContainer{
    position: absolute;
    height: 100vh;
    width: 100vw;
    pointer-events: auto;
}
#leftArrow,#rightArrow{
    position: absolute;
    height: 40px;
    z-index: 1000;
    top: 50%;
    border-radius: 20px;
}
#leftArrow{
    right: 0;
}
#rightArrow{
    left: -10px;
}
#leftArrow:hover,#rightArrow:hover{
    cursor: pointer;
    background-color: aliceblue;
}
</style>
<style>
.amap-logo ,.amap-copyright {
  opacity: 0 !important;
}
.el-tabs__content{
    display: none;
}

</style>
