<template>
    <el-dialog 
        id="pointDialog"
        v-model="pointDialog" 
        title="选择变电站"
        width="40%"
        top="25vh">
        <template #header="{ close, titleId, titleClass }">
            <div class="my-header">
                <el-input
                    v-model="inputValue"
                    @clear="inputClear"
                    placeholder="搜索变电站"
                    :prefix-icon="Search"
                    clearable
                    size="small"
                    />
                <el-button
                    size="small"
                    type="primary"
                    @click="searchPoint">
                    搜索
                </el-button>
                <!-- <el-checkbox
                    size="large" 
                    v-model="golbalSearch"
                    @change="golbalSearchChange"/> -->
            </div>
        </template>
        <el-form 
            :model="form"
            :inline="true"
            size="small">
            <el-form-item label="电压等级">
                <el-select 
                    v-model="form.nodeVolLevel" 
                    placeholder="电压等级"
                    :multiple="true"
                    :clearable="true"
                    :collapse-tags="true"
                    :collapse-tags-tooltip="true"
                    @change="change">
                    <el-option
                        v-for="item in voltages"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"    
                        ></el-option>
                </el-select>
                <el-checkbox
                    size="large" 
                    v-model="voltageChecked"
                    @change="voltageCheckedChange"
                    :indeterminate="voltageIndeterminate"/>
            </el-form-item>
            <el-form-item label="类型">
                <el-select 
                    v-model="form.nodeVariable" 
                    placeholder="变电站类型"
                    :multiple="true"
                    :clearable="true"
                    :collapse-tags="true"
                    :collapse-tags-tooltip="true"
                    @change="change">
                    <el-option
                        v-for="item in types"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"    
                        ></el-option>
                </el-select>
                <el-checkbox
                    size="large" 
                    v-model="typeChecked"
                    @change="typeCheckedChange"
                    :indeterminate="typeIndeterminate"/>
            </el-form-item>
            <el-form-item label="城市">
                <el-select 
                    v-model="form.city" 
                    placeholder="城市"
                    :multiple="true"
                    :clearable="true"
                    :collapse-tags="true"
                    :collapse-tags-tooltip="true"
                    @change="change">
                    <el-option
                        v-for="item in citys"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"    
                        ></el-option>
                </el-select>
                <el-checkbox
                    size="large" 
                    v-model="cityChecked"
                    @change="cityCheckedChange"
                    :indeterminate="cityIndeterminate"/>
            </el-form-item>
        </el-form>

      
        <el-table 
            :data="tableData" 
            height="25vh" 
            highlight-current-row
            @current-change="handleCurrentChange"
            style="width: 100%">
            <el-table-column prop="nodeNameStart" label="名称"/>
            <el-table-column prop="nodeVariable" label="类型"/>
            <el-table-column prop="city" label="城市" />
            <el-table-column prop="nodeVolLevel" label="电压等级(kV)" />
        </el-table>
        <template #footer>
            <span class="dialog-footer">
                <el-button @click="pointDialog = false">取消</el-button>
                <el-button type="primary" @click="sendNodeNum">确定</el-button>
            </span>
        </template>
    </el-dialog>
    

</template>

<script setup>
import { ref, inject, reactive } from 'vue';
import { url } from '@/websocketConfig.js'
import { Search } from '@element-plus/icons-vue'
import axios from 'axios';
let emits = defineEmits(['selectedPointNum'])
let pointDialog = inject('pointDialog')
let inputValue = ref('')
let pointNum =  inject('pointNum')
let currentPointList = {}
let form = reactive({
    nodeVolLevel:[],
    city:[],
    nodeVariable:[]
})
let tableData = reactive([])
let voltageChecked = ref(false)
let voltageIndeterminate = ref(false)
let typeChecked = ref(false)
let typeIndeterminate = ref(false)
let cityChecked = ref(false)
let cityIndeterminate = ref(false)
let voltages = [
    {
        label:'220kV',
        value:220
    },
    {
        label:'500kV',
        value:500
    },
    {
        label:'1000kV',
        value:1000
    }
]
let citys = [
    {
        label:'杭州',
        value:'杭州'
    },
    {
        label:'宁波',
        value:'宁波'
    },
    {
        label:'嘉兴',
        value:'嘉兴'
    },
    {
        label:'湖州',
        value:'湖州'
    },
    {
        label:'金华',
        value:'金华'
    },
    {
        label:'衢州',
        value:'衢州'
    },
    {
        label:'温州',
        value:'温州'
    },
    {
        label:'台州',
        value:'台州'
    },
    {
        label:'丽水',
        value:'丽水'
    },
    {
        label:'舟山',
        value:'舟山'
    },
    {
        label:'绍兴',
        value:'绍兴'
    }
]
let types = [
    {
        label:'变电站',
        value:'变电站'
    },
    {
        label:'牵引站',
        value:'牵引站'
    },
    {
        label:'换流站',
        value:'换流站'
    },
    {
        label:'火电站',
        value:'火电站'
    },
    {
        label:'水电站',
        value:'水电站'
    },
    {
        label:'抽水蓄能电站',
        value:'抽水蓄能电站'
    },
    {
        label:'核电站',
        value:'核电站'
    },
    {
        label:'光伏电站',
        value:'光伏电站'
    },
    {
        label:'风电站',
        value:'风电站'
    },
    {
        label:'调相机',
        value:'调相机'
    },
    {
        label:'储能站',
        value:'储能站'
    }
]

function change(value){
    voltageChecked.value = form.nodeVolLevel.length === voltages.length
    voltageIndeterminate.value = form.nodeVolLevel.length > 0 && form.nodeVolLevel.length < voltages.length

    typeChecked.value = form.nodeVariable.length === types.length
    typeIndeterminate.value = form.nodeVariable.length > 0 && form.nodeVariable.length < types.length

    cityChecked.value = form.city.length === citys.length
    cityIndeterminate.value = form.city.length > 0 && form.city.length < citys.length
    sendData()
}
function voltageCheckedChange(value){
    voltageIndeterminate.value = false
    value? form.nodeVolLevel = voltages.map(item => item.value) : form.nodeVolLevel = []
    sendData()
}
function typeCheckedChange(value){
    typeIndeterminate.value = false
    value? form.nodeVariable = types.map(item => item.value) : form.nodeVariable = []
    sendData()
}
function cityCheckedChange(value){
    cityIndeterminate.value = false
    value? form.city = citys.map(item => item.value) : form.city = []
    sendData()
}
function searchPoint(){
    axios.post(url+'/getNodeInfoStation1',{name:inputValue.value}).
        then((res) => {
            tableData.length = 0
            res.data.nodeInfoEntityList.map(item => {
                tableData.push(item)
            })
        })
}

function inputClear(){
    sendData()
}
function sendData(){
    axios.post(url+'/getNodeInfoStation',form).
        then((res) => {
            tableData.length = 0
            res.data.nodeInfoEntityList.map(item => {
                tableData.push(item)
            })
        })
}
function handleCurrentChange(value){
    currentPointList = value
}
function sendNodeNum(){
    pointDialog.value = false
    pointNum.value = currentPointList.nodeNum
    emits('selectedPointNum',currentPointList)
}
</script>
<style lang="less">
.el-dialog__header{
    padding-left:2vh;
}
.el-dialog__body{
    padding: 0 2vh;
}


#lineDialog > .el-checkbox-group,.el-checkbox{
    height: 100% !important;
}
.el-form-item__content{
    flex-wrap: nowrap;
} 
.el-form--inline .el-form-item{
    margin-right: 1%;
}

#pointDialog  .el-form-item{
    margin-bottom: 0;
    width: 32%;
}
#pointDialog .el-select{
    width: 90%;
}
.my-header{
    .el-input{
        width: 9vw;
    }
}
</style>