<template>
    
    <div id="simulateOperation">
        <div>
          <el-button
            @click="startSimulate"
            :disabled="simStatus"
            class="simButton" 
            plain
            size="small"
            
            >{{simStatus ? '运行中':'开始运行'}}({{ progress }}%)
            <span id="progress"></span>
          </el-button>
        </div>
        <div>
          <el-button
            @click="stopSimulate"
            :disabled="!simStatus"
            class="simButton" 
            plain
            size="small">停止运行</el-button>
        </div>
        
        <div id="outputData">
          <span  @dblclick="dblclick">新能源装机(MW)：
            <span v-if="view">{{newEnergyCapacity}}</span>
          </span>
          <el-input
              v-model="inputNum"
              @blur="inputBlur"
              @keyup.enter="inputBlur"
              :validate-event="true"
              ref="refInput"
              v-if="!view"
              :min="0"
              size="small"
              controls-position="right"/>
        </div>
        
        <div id="simulateChart"></div>
    </div>
    
</template>

<script setup>
import { onMounted, ref, nextTick } from 'vue'
import { fontSize } from '@/util/util.js'
import * as echarts from 'echarts'
import { ElMessage } from 'element-plus'
import axios from 'axios'
import { url } from "@/websocketConfig.js"
let props = defineProps(['simulateData'])
let newEnergyCapacity = ref(123)
let inputNum = ref(0)
let view = ref(true)
let refInput = ref()
let progress = ref(0)
let progressSpan
let colors = ['#6ad8ff','#FFC56C', 'rgba(0,255,127, 1)','#a6d8ff']
let options = {
    title:{
      left:0,
      textStyle:{
        color:colors[0],
        fontSize:fontSize(12),
        fontWeight:'normal'
      }
    },
    xAxis: {
      type: 'category',
      axisTick:{
        show:false
      },
      axisLine:{
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      splitLine:{
        show:false
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
    },
    yAxis: {
      type: 'value',
      splitLine:{
        show:false
      },
      axisLine:{
        show:true,
        lineStyle:{
          color:colors[3],
          width:1
        }
      },
      axisLabel:{
        fontSize:fontSize(10),
      },
      splitNumber:3
    },
    legend:{
        icon: 'circle',
        top:0,
        itemWidth:fontSize(10),
        textStyle: {
            color: colors[0],
            fontSize:fontSize(10)
        },
    },
    tooltip:{
        show:true,
        trigger:'axis',
        snap:true,
        axisPointer:{
          type:'line',
        },
        enterable:true,
        triggerOn:'click'
    },
    
    series: [
        {
            name:'传统能源出力',
            type:'line',
            smooth: true,
            symbol:"none",
            itemStyle:{
                color:colors[0]
            },
            lineStyle:{
                width:fontSize(2)
            },
        },
        {
            name:'新能源出力',
            type:'line',
            smooth: true,
            symbol:"none",
            itemStyle:{
                color:colors[1]
            },
            lineStyle:{
                width:fontSize(2)
            },
        },
        {
            name:'负荷',
            type:'line',
            smooth: true,
            symbol:"none",
            itemStyle:{
                color:colors[2]
            },
            lineStyle:{
                width:fontSize(2)
            },
        }
    ],
    grid:{
      show:false,
      left:'0%',
      right:'5%',
      top:'20%',
      bottom:'0%',
      containLabel: true
    }
}
let simStatus = ref(false)
let simulateChart
onMounted(() => {
    // getCapacity()
    getButtonStatus()
    simulateChart = echarts.init(document.getElementById('simulateChart'));
    simulateChart.setOption(options);
    setCharts()
    window.addEventListener('resize',() => {
        simulateChart.resize()
    })
    

    setInterval(() => {
      progress.value++
      progressSpan.style.left = (-100+progress.value)+'%'
      if(progress.value==120){
        progress.value = 0
      }
    },500)
    progressSpan = document.getElementById('progress')

    
})

function setCharts(){
    simulateChart.setOption({
      xAxis:{
        data:['2021','2022','2023']
      },
      series:[
        {
          name:'新能源出力',
          data:props.simulateData.newOutput,
        },
        {
          name:'传统能源出力',
          data:props.simulateData.oldOutput,
        },
        {
          name:'负荷',
          data:props.simulateData.load,
        },
      ]
    });
}



function dblclick(){
    view.value = false
    nextTick(() => {
        refInput.value.focus()
        inputNum.value = newEnergyCapacity.value
    })
    
}
let regexp = new RegExp(/^[1-9]\d*\.\d+$|^0\.\d+$|^[1-9]\d*$|^0$/)

function inputBlur(){
    if(!regexp.test(inputNum.value)){
      ElMessage.error('请输入数字')
      refInput.value.focus()
      return
    }
    newEnergyCapacity.value = Number(inputNum.value)
    view.value = true
}

function startSimulate(){
    axios.post(url+'/simStart').then((res) => {
      simStatus.value = true
    })
}

function stopSimulate(){
    axios.post(url+'/simStop').then((res) => {
      simStatus.value = false
    })
}
function getCapacity(){
    axios.post(url+'',{
        capacity:newEnergyCapacity.value
    }).then((res) => {
      newEnergyCapacity.value = res.data
      inputNum.value = newEnergyCapacity.value
    })
}
function getButtonStatus(){
  axios.post(url+'/getButtonStatus',{}).
        then((res) => {
          simStatus.value = res.data
          console.log(res.data)
        })
}
</script>

<style>
#simulateOperation{
    display: grid;
    grid-template-columns: 1fr 1fr 1.5fr ;
}
#simulateOperation > div:nth-child(-n+2){
    text-align: center;
    align-items: center;
    width: fit-content;
    margin: auto;
}
#outputData{
    display: flex;
    align-items: center;
    user-select: none;
}
#outputData:hover{
    cursor: pointer;
}
#outputData > span{
    white-space: nowrap;
}
#simulateChart{
    height: 13vh;
    grid-column: 1/5;
    margin-top: 1vh;
}
.simButton{
    position: relative;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0);
    color: #a6d8ff;
    min-width: 105px;
}
.simButton:hover{
    background-color: rgba(0, 0, 0, 0);
    color: #6ad8ff;
}
#progress {
    content: '';
    position: absolute;
    top: 90%;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #00ffff00, #0ff); 
}
.simButton.is-disabled{
  background-color: rgba(0, 0, 0, 0) ;
}
.simButton.is-disabled:hover{
  background-color: rgba(0, 0, 0, 0) ;
}
</style>