<script setup>
import HomeButon from './homeButon.vue';
import { onMounted, ref } from 'vue';
import tran220 from "../../assets/img/220变电站.png"
import tran500 from "../../assets/img/500变电站.png"
import tran1000 from "../../assets/img/1000变电站.png"
let menu = ref([]);
let active = ref(1)
onMounted(() => {
    let bg = document.getElementById('bg')
    let item = document.getElementsByClassName('item')
    let spanWidth = item[active.value-1].firstChild.offsetWidth
    let itemWidth = item[active.value-1].clientWidth
    bg.style.width = spanWidth+'px'
    bg.style.left = ((active.value-1)*itemWidth + itemWidth/2 - spanWidth/2)+'px'
})
function test(){
    let bg = document.getElementById('bg')
    let item = document.getElementsByClassName('item')
    let spanWidth = item[active.value-1].firstChild.offsetWidth
    let itemWidth = item[active.value-1].clientWidth
