<template>
    <div id="leftData">
        <div>
            <div class="itemTitle">
                <span>电网概况</span>
            </div>
            <div class="border borderCorner">
                <GridScale></GridScale>
            </div>
        </div>
        <div>
            <div class="itemTitle">
                <span>模拟运行</span>
            </div>
            <div class="border borderCorner">
                <SimulateOperation
                    :simulateData="simulateData"></SimulateOperation>
            </div>
        </div>
        <div>
            <div class="itemTitle faultTitle">
                <span>薄弱环节诊断</span>
                <div id="setting"
                    @click="settingDialog = true">
                    <img src="@/assets/img/setting.svg" alt="">
                </div>
            </div>
            <SettingDialog></SettingDialog>
            <div class="border borderCorner" id="faultInformation">
                <Fault
                    :faultData="faultData"></Fault>
            </div>
        </div>
        <div>
            <div class="itemTitle">
                <span>电压、频率监测</span>
            </div>
            <div class="border borderCorner voltageMonitor">
                <VoltageMonitor
                    :freVoltage="freVoltage"
                    ref="voltageMonitorRef"></VoltageMonitor>
            </div>
        </div>
       
    </div>
</template>

<script setup>
import { onMounted, ref, watch, reactive, nextTick, onUnmounted, provide } from 'vue'
import { ConnectWebsocket } from '@/websocketConfig';
import Fault from './Fault.vue';
import VoltageMonitor from './VoltageMonitor.vue';
import GridScale from './GridScale.vue';
import SimulateOperation from './SimulateOperation.vue';
import SettingDialog from './SettingDialog.vue';
import Charts from '../rightData/charts.vue'
import { ElMessage } from 'element-plus'
let subscribes = []
let xAxis = reactive([])
let voltage = reactive({
    voltage220:[],
    voltage500:[],
    voltage1000:[],
})
let simulateData = reactive({
    newOutput:[],
    oldOutput:[],
    load:[],
})
let faultData = reactive({
    volFaults:[],
    lineLoadRateFaults:[],
    nodeLoadRateFaults:[],
    freqFaults:[],
})
let voltageMonitorRef = ref()
let freVoltage = reactive({
    xAxisData:[],
    freq:[],
    voltage220:[],
    voltage500:[],
    voltage1000:[],
})
let settingDialog = ref(false)
provide('settingDialog',settingDialog)
onMounted(() => {
    let websocketService = new ConnectWebsocket()
    let client =  websocketService.client
    client.activate()
    client.onConnect = () => {
        subscribes.push(client.subscribe('/user/response/getVolAndFreq',(msg) => {
            freVoltageMonitor(JSON.parse(msg.body))
        }))
        subscribes.push(client.subscribe('/user/response/getFaults',(msg) => {
            getFaults(JSON.parse(msg.body))
        }))
        subscribes.push(client.subscribe('/user/response/getOutput',(msg) => {
            getOutput(JSON.parse(msg.body))
        }))
        client.publish({
            destination:'/app/getVolAndFreq',
            body:JSON.stringify({}),
        })
        client.publish({
            destination:'/app/getFaults',
            body:JSON.stringify({}),
        })
        client.publish({
            destination:'/app/getOutput',
            body:JSON.stringify({}),
        })
    }
})
onUnmounted(() => {
    subscribes.map(item => {item.unsubscribe()})
})

function getFaults(msg){
    if(msg.status === 'FAILED'){
        ElMessage.error('薄弱环节诊断数据获取失败')
    }else if(msg.status === 'SUCCESS'){
        for(let i in faultData){
            faultData[i].length = 0
        }
        msg.volFaults.map(item => {
            faultData.volFaults.push({
                nodeNum:item.nodeNum,
                time:item.time,
                voltage:item.nodeNameStart+'('+item.voltageA+'kV)'
            })
        })
        msg.lineLoadRateFaults.map(item => {
            faultData.lineLoadRateFaults.push({
                nodeNum:item.nodeNum,
                time:item.time,
                loadRate:item.nodeNameStart+'-'+item.nodeNameEnd+'('+item.loadRate+'%)'
            })
        })
        msg.nodeLoadRateFaults.map(item => {
            faultData.nodeLoadRateFaults.push({
                nodeNum:item.nodeNum,
                time:item.time,
                loadRate:item.nodeNameStart+'('+item.loadRate+'%)'
            })
        })
        msg.freqFaults.map(item => {
            faultData.freqFaults.push(item)
        })
    }
}

function freVoltageMonitor(msg){
    if(msg.status === 'FAILED'){
        ElMessage.error('电压、频率监测数据获取失败')
    }else if(msg.status === 'SUCCESS'){
        console.log(msg)
        if(msg.freqMonitorEntityList.length > 1){
            for(let i in freVoltage){
                freVoltage[i].length = 0
            }
        }
        msg.freqMonitorEntityList.map(item => {
            freVoltage.freq.push(item.freq)
            freVoltage.xAxisData.push(item.time)
        })
        msg.volMonitorEntityList220.map(item => {
            freVoltage.voltage220.push({
                value:item.voltageA,
                nodeNum:item.nodeNum,
                nodeName:item.nodeNameStart
            })
        })
        msg.volMonitorEntityList500.map(item => {
            freVoltage.voltage500.push({
                value:item.voltageA,
                nodeNum:item.nodeNum,
                nodeName:item.nodeNameStart
            })
        })
        msg.volMonitorEntityList1000.map(item => {
            freVoltage.voltage1000.push({
                value:item.voltageA,
                nodeNum:item.nodeNum,
                nodeName:item.nodeNameStart
            })
        })
        voltageMonitorRef.value.setCharts()
    }
}

function getOutput(msg){
    if(msg.status === 'FAILED'){
        ElMessage.error('模拟运行数据获取失败')
    }else if(msg.status === 'SUCCESS'){
        if(msg.times.length > 1){
            for(let i in simulateData){
                simulateData[i].length = 0
            }
        }
        msg.volFaults.map(item => {
            simulateData.newOutput.push(item.a)
        })
        msg.volFaults.map(item => {
            simulateData.oldOutput.push(item.a)
        })
    }
}





</script>

<style lang="less" scoped>

#leftData{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 1000;
}
.itemTitle{
    margin: 0 1vh 1vh 1vh;
    height: 3vh;
    display: flex;
    color: #fff;
    border-bottom: 0.2vh solid;
    border-image:linear-gradient(90deg,rgba(106,216,255,0) 0%,rgba(106,216,255,1) 25%,rgba(106,216,255,0) 100%) 1 1 1 1;
    span{
        font-size: 1.5vh;
        line-height: 3vh;
    }
}
.itemTitle::before{
    content:'';
    width: 1.5vh;
    height: 1.5vh;
    margin: auto 0;
    margin-right: 1vh;
    background: url(@/assets/img/菱形.png);
    background-size: cover;
}
#setting{
    height: 2vh;
    width: 2vh;
    margin: auto;
    padding: 0.5vh;
    margin-right: 0;
    border-radius: 50%;
    img{
        width: 100%
    }
}
#setting:hover{
    background-color: #ffffffa0;
    cursor: pointer;
}
.borderCorner{
    position: relative;
    margin: 0 1vh;
    transition: all 1s;
    padding: 1vh;
    font-size: 1vh;
    color:rgba(106,216,255,1);
}
.voltageMonitor{
    margin-bottom: 1vh;
}



</style>